@import url(https://fonts.googleapis.com/css?family=Inconsolata:700);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  background: rgba(28,131,174,255) url(/PokemonReact/static/media/wallpaper.64c96d29.gif);
  background-size:100%;
  background-repeat:no-repeat;
  background-attachment:fixed;
  background-position:bottom right;
}

*{margin:0;padding:0;box-sizing:border-box}html,body{width:100%;height:100%}.header{display:-webkit-flex;display:flex;color:white;font-size:xx-large;width:100%;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-top:5%}.home-button{background:transparent;border:0}.descripcion{width:80%;margin-top:6%}.search-box{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.container{position:relative;margin-top:10%;top:0;left:0;right:0;bottom:0;width:300px;height:100px}.container .search{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:120px;height:120px;background:#ffcd00;border-radius:50%;transition:all 1s;z-index:4;box-shadow:0 0 25px 0 rgba(0,0,0,0.4)}.container .search:hover{cursor:pointer}.container .search::before{content:"";position:absolute;margin:auto;top:35px;right:0;bottom:0;left:30px;width:24px;height:2px;background:white;-webkit-transform:rotate(45deg);transform:rotate(45deg);transition:all .5s}.container .search::after{content:"   ";position:absolute;margin:auto;top:-5px;right:0;bottom:0;left:-5px;width:25px;height:25px;border-radius:50%;border:2px solid white;transition:all .5s}.container input{font-family:'Inconsolata', monospace;position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:80px;height:60px;outline:none;border:none;background:#ffcc004d;color:white;text-shadow:0 0 10px rgba(255,204,0,0.267);padding:0 80px 0 20px;border-radius:30px;box-shadow:0 0 25px 0 #ffcd00,0 20px 25px 0 rgba(0,0,0,0.2);transition:all 1s;opacity:0;z-index:5;font-weight:bolder;letter-spacing:0.1em}.container input:hover{cursor:pointer}.container input:focus{width:300px;opacity:1;cursor:text}.container input:focus ~ .search{right:-250px;background:#151515;z-index:6}.container input:focus ~ .search::before{top:0;left:0;width:25px}.container input:focus ~ .search::after{top:0;left:0;width:25px;height:2px;border:none;background:white;border-radius:0%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.container input::-webkit-input-placeholder{color:white;opacity:0.5;font-weight:bolder}.container input:-ms-input-placeholder{color:white;opacity:0.5;font-weight:bolder}.container input::placeholder{color:white;opacity:0.5;font-weight:bolder}.isLoading{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;height:100%;margin:50%;width:10%}.provider{display:block}.poklist{margin:3%;box-shadow:4px 6px 19px -10px rgba(98,25,165,0.651);background:linear-gradient(to top, rgba(8,23,155,0.589), transparent);border:0;padding:0;border-radius:25%}.poklist:hover{box-shadow:4px 6px 19px -10px rgba(255,255,255,0.774)}.poktext{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-decoration:none;font-size:20pt;color:white;-webkit-align-items:center;align-items:center;text-transform:capitalize}.list{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.info{color:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;font-family:'Roboto Condensed', sans-serif;font-size:x-large}.icon{object-fit:cover}.not-found{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;margin-top:10%}.pokegraph{width:500px;height:500px}

